.pdf-viewer-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
}

.pdf-header {
  margin-bottom: 2rem;
}

.pdf-header h2 {
  color: #0066CC;
  margin-bottom: 0.5rem;
}

.pdf-info {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  color: #666;
  font-size: 0.9rem;
}

.pdf-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  background-color: #f5f7fa;
  padding: 0.75rem;
  border-radius: 4px;
}

.control-button {
  background-color: #0066CC;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.control-button:hover:not(:disabled) {
  background-color: #0052a3;
}

.control-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.page-info {
  margin: 0 1rem;
  font-weight: 500;
}

.pdf-document-container {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  background-color: #f5f7fa;
  padding: 1rem;
  border-radius: 4px;
  min-height: 500px;
}

.pdf-document {
  max-width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.pdf-page {
  max-width: 100%;
  height: auto !important;
}

.pdf-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
}

.action-link {
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  text-decoration: none;
  font-weight: bold;
  transition: all 0.3s ease;
}

.qr-link {
  background-color: #28a745;
  color: white;
}

.qr-link:hover {
  background-color: #218838;
}

.download-link {
  background-color: #0066CC;
  color: white;
}

.download-link:hover {
  background-color: #0052a3;
}

.back-link {
  background-color: #6c757d;
  color: white;
}

.back-link:hover {
  background-color: #5a6268;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  font-size: 1.2rem;
  color: #666;
}

.error-container {
  background-color: #f8d7da;
  color: #721c24;
  padding: 1rem;
  border-radius: 4px;
  margin: 2rem auto;
  max-width: 600px;
  text-align: center;
}

@media (max-width: 768px) {
  .pdf-viewer-container {
    padding: 1rem;
  }
  
  .pdf-info {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .pdf-actions {
    flex-direction: column;
  }
  
  .action-link {
    text-align: center;
  }
}