.category-view-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.category-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.category-title {
  display: flex;
  align-items: center;
}

.category-icon {
  font-size: 2rem;
  margin-right: 1rem;
}

.category-title h2 {
  color: #0066CC;
  margin: 0;
}

.search-container {
  flex: 1;
  max-width: 400px;
}

.search-input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

.search-input:focus {
  border-color: #0066CC;
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 102, 204, 0.2);
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  font-size: 1.2rem;
  color: #666;
}

.no-pdfs {
  text-align: center;
  padding: 3rem;
  background-color: #f5f7fa;
  border-radius: 8px;
}

.no-pdfs p {
  margin-bottom: 1.5rem;
  color: #666;
  font-size: 1.1rem;
}

.upload-link {
  background-color: #0066CC;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.upload-link:hover {
  background-color: #0052a3;
}

.pdf-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.pdf-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.pdf-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}

.pdf-card-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  text-align: center;
}

.pdf-card-content {
  flex: 1;
}

.pdf-card-title {
  color: #333;
  margin: 0 0 0.5rem 0;
  font-size: 1.2rem;
}

.pdf-card-date, .pdf-card-size {
  color: #666;
  margin: 0.25rem 0;
  font-size: 0.9rem;
}

.pdf-card-actions {
  display: flex;
  gap: 0.75rem;
  margin-top: 1.5rem;
}

.pdf-action-button {
  flex: 1;
  padding: 0.5rem 0;
  border-radius: 4px;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  font-size: 0.9rem;
  transition: background-color 0.3s ease;
}

.view-button {
  background-color: #0066CC;
  color: white;
}

.view-button:hover {
  background-color: #0052a3;
}

.qr-button {
  background-color: #28a745;
  color: white;
}

.qr-button:hover {
  background-color: #218838;
}

.category-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
}

.category-action-button {
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.upload-button {
  background-color: #0066CC;
  color: white;
}

.upload-button:hover {
  background-color: #0052a3;
}

.home-button {
  background-color: #6c757d;
  color: white;
}

.home-button:hover {
  background-color: #5a6268;
}

@media (max-width: 768px) {
  .category-view-container {
    padding: 1rem;
  }
  
  .category-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .search-container {
    width: 100%;
    max-width: none;
  }
  
  .pdf-grid {
    grid-template-columns: 1fr;
  }
  
  .category-actions {
    flex-direction: column;
  }
  
  .category-action-button {
    width: 100%;
    text-align: center;
  }
}