.qr-generator-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}

.qr-generator-container h2 {
  color: #0066CC;
  margin-bottom: 1.5rem;
}

.qr-info {
  margin-bottom: 2rem;
}

.qr-info p {
  margin-bottom: 0.5rem;
  color: #555;
}

.qr-url {
  background-color: #f5f7fa;
  padding: 0.75rem;
  border-radius: 4px;
  word-break: break-all;
  font-size: 0.9rem;
}

.qr-url a {
  color: #0066CC;
  text-decoration: none;
}

.qr-url a:hover {
  text-decoration: underline;
}

.qr-code-container {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.qr-code {
  padding: 1rem;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.qr-actions {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-width: 300px;
  margin: 0 auto;
}

.download-qr-button, .view-pdf-button, .back-button {
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-weight: bold;
  transition: all 0.3s ease;
  cursor: pointer;
}

.download-qr-button {
  background-color: #28a745;
  color: white;
  border: none;
}

.download-qr-button:hover {
  background-color: #218838;
}

.view-pdf-button {
  background-color: #0066CC;
  color: white;
  text-decoration: none;
}

.view-pdf-button:hover {
  background-color: #0052a3;
}

.back-button {
  background-color: #6c757d;
  color: white;
  text-decoration: none;
}

.back-button:hover {
  background-color: #5a6268;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  font-size: 1.2rem;
  color: #666;
}

.error-container {
  background-color: #f8d7da;
  color: #721c24;
  padding: 1rem;
  border-radius: 4px;
  margin: 2rem auto;
  max-width: 600px;
  text-align: center;
}

@media (max-width: 768px) {
  .qr-generator-container {
    padding: 1rem;
  }
  
  .qr-code {
    max-width: 100%;
    height: auto;
  }
}